import $ from 'jquery';
import SyntaxHighlighterController from './controller/SyntaxHighlighter.controller';
import ModifierClassSwitchController from './controller/ModifierClassSwitch.controller';
import IconGuidelinesController from './controller/IconGuidelines.controller';
import { IDENTIFIERS } from './constants/Identifiers.const';

/**
 * Class wrapping all uikit framework features.
 * Is used as a singleton.
 * @returns {void}
 */
class UiKit {

	/**
	 * Initialises the controller singleton.
	 * Has to be called after ready event.
	 * @public
	 * @returns {void}
	 */
	init () {
		SyntaxHighlighterController.init();
		IconGuidelinesController.init( $( '.ui-kit-icon-list' ) );

		$.each( $( IDENTIFIERS.MODIFIERCLASSSWITCHER.ROOT ), function initInstance () {
			const controllerInstance = new ModifierClassSwitchController();

			controllerInstance.init( $( this ) );
		} );
	}

}

const instance = new UiKit();

instance.init();

/**
 * @public
 * @return {UiKit} singleton
 */
export default instance;



