import AbstractController from './../../../base/controller/Abstract.controller.js';
import $ from 'jquery';

/**
 * Add prefix and suffix to string defined in an obj
 * @param  {String} what         String to prefix and suffix
 * @param  {Object} obj          Object with values to add as prefix / suffix
 * @param  {String} [obj.prefix] Optional string to add as prefix
 * @param  {String} [obj.suffix] Optional string to add as suffix
 * @return {String}              Prefixed and suffixed string
 */
function prefSuffIxString ( what, obj ) {
	return ( obj.prefix ? obj.prefix : '' ) + what + ( obj.suffix ? obj.suffix : '' );
}

/**
 * Add prefix and suffix to a variable. Uses {@link prefSuffIxString}
 * @param  {(Array|String)} what    Object to add prefix and suffix
 * @param  {Object} obj             Object with values to add as prefix / suffix
 * @param  {String} [obj.prefix]    Optional string to add as prefix
 * @param  {String} [obj.suffix]    Optional string to add as suffix
 * @return {(Array|String|Boolean)} Returns prefixed Object or false, if Object type is not supported
 */
function prefSuffIxIt ( what, obj ) {
	if ( Array.isArray( what ) ) {
		return what.map( ( v ) => {
			return prefSuffIxIt( v, obj );
		} ).filter( Boolean );
	} else if ( typeof what === 'string' && what !== '' ) {
		return prefSuffIxString( what, obj );
	}
	
	return false;
}

/**
 * Update the classes on an DOM Element item
 * @param  {DOMElement}     item              DOM Element to update
 * @param  {Array.<String>} cls               Classes to update item with
 * @param  {Object}         modifies          The currently used modifies object
 * @param  {String}         [modifies.prefix] Optional string to add as prefix
 * @param  {String}         [modifies.suffix] Optional string to add as suffix
 * @return {void}
 */
function updateClassOnItem ( item, cls, modifies ) {
	const currentClasses = item.classList;
	let oldClass = prefSuffIxIt( this.currentSelectValue, modifies );

	if ( oldClass !== false ) {
		if ( !Array.isArray( oldClass ) ) {
			oldClass = [ oldClass ];
		}
		oldClass.forEach( ( v ) => {
			if ( !cls.includes( v ) ) {
				currentClasses.remove( v );
			} else {
				cls.splice( cls.indexOf( v ) );
			}
		} );
	}

	if ( cls.length ) {
		cls.forEach( ( v ) => {
			currentClasses.add( v );
		} );
	}
}

/**
 * Update the classes on DOM Elements. Uses {@link updateClassOnItem}
 * @param  {Array.<DOMElement>} items             DOM Elements to update
 * @param  {Array.<String>}     cls               Classes to update item with
 * @param  {Object}             modifies          The currently used modifies object
 * @param  {String}             [modifies.prefix] Optional string to add as prefix
 * @param  {String}             [modifies.suffix] Optional string to add as suffix
 * @return {void}
 */
function updateClassOnItems ( items, cls, modifies ) {
	$( items ).each( ( i, item ) => {
		updateClassOnItem.call( this, item, cls, modifies );
	} );
}

/**
 *  @private
 *  @return { void }
 */
function addHandler () {
	this.$select.on( 'change', ( e ) => {
		const selectValue = this.$select.val().split( ' ' );

		this.modifies.forEach( ( v ) => {
			const $currentItem = this.$example.find( v.selector ),
						cls = prefSuffIxIt( selectValue, v );

			if ( $currentItem.length ) {
				updateClassOnItems.call( this, $currentItem, cls, v );
			}
		} );

		if ( selectValue ) {
			this.currentSelectValue = selectValue;
		} else {
			this.currentSelectValue = '';
		}

	} );
}

/**
 * Class representing an optional class switch instance.
 * Controls the state of the optional class switch.
 * @extends { AbstractController }
 */
class ModifierClassSwitchController extends AbstractController {

	constructor () {
		super();

		this._isIL01 = false;
	}

	/**
	 * Initialises the controller.
	 * Has to be called after ready event.
	 *
	 * @param { object } $root - jQuery wrapped DOM node
	 * @public
	 * @return { void }
	 */
	init ( $root ) {

		this.$scope   = $root;
		this.$select  = this.$scope.children( 'select' );
		this.$example = this.$scope.siblings( '.uikit-preview-example' );

		if ( this.$select[ 0 ].hasAttribute( 'data-modifies' ) ) {
			this.modifies = JSON.parse( this.$select.attr( 'data-modifies' ) );
		} else {
			this.modifies = [ {
				selector : '>:first-child',
				prefix :   '',
				suffix :   ''
			} ];
		}

		this.initialSelectValue = this.$select.val();

		this.currentSelectValue = this.initialSelectValue;

		addHandler.call( this );
	}
}

export default ModifierClassSwitchController;



