import AbstractController from './../../../base/controller/Abstract.controller.js';

import $ from 'jquery';

/**
 * Class representing an optional class switch instance.
 * Controls the state of the optional class switch.
 * @extends { AbstractController }
 */
class IconGuidelinesController extends AbstractController {

	constructor () {
		super();
	}

	/**
	 * Initialises the controller.
	 * Has to be called after ready event.
	 *
	 * @param { object } $root - jQuery wrapped DOM node
	 * @public
	 * @return { void }
	 */
	init ( $root ) {

		this.$scope   = $root;
		this.$listItems  = this.$scope.children( 'li' );
		this.$guidelines = $( '<div />' ).css( {
			position : 'absolute',
			left :     0,
			top :      0
		} );

		const itemCount = this.$listItems.length,
					itemCols = Math.floor( this.$scope.outerWidth() / this.$listItems.outerWidth() ),
					itemRows = Math.ceil( itemCount / itemCols );

		this.$scope.parent().css( {
			position : 'relative'
		} ).append( this.$guidelines );

		$( window ).on( 'load', () => {
			outerLoop : for ( let i = 0; i < itemRows; i++ ) {
				for ( let j = 0; j < itemCols; j++ ) {
					const curIndex = i * itemCols + j,
								currentItem = this.$listItems.eq( curIndex ),
								currentItemIcon = currentItem.find( 'i' );

					if ( curIndex >= itemCount ) {
						break outerLoop;
					}

					if ( i === 0 ) {
						const _left = currentItem.position().left + currentItemIcon.offset().left - currentItem.offset().left,
									$guideline = $( '<div />' ).css( {
										position :   'absolute',
										width :      1,
										height :     this.$scope.outerHeight(),
										background : 'rgba(0, 255, 255, 0.3)',
										top :        0,
										left :       _left
									} ),
									$guideline2 = $( '<div />' ).css( {
										position :   'absolute',
										width :      1,
										height :     this.$scope.outerHeight(),
										background : 'rgba(0, 255, 255, 0.3)',
										top :        0,
										left :       _left + currentItemIcon.outerWidth()
									} );

						this.$guidelines.append( $guideline );
						this.$guidelines.append( $guideline2 );
					}
					
					if ( j === 0 ) {
						const _top = currentItem.position().top + currentItemIcon.offset().top - currentItem.offset().top,
									$guideline = $( '<div />' ).css( {
										position :   'absolute',
										width :      this.$scope.outerWidth(),
										height :     1,
										background : 'rgba(0, 255, 255, 0.3)',
										top :        _top,
										left :       0
									} ),
									$guideline2 = $( '<div />' ).css( {
										position :   'absolute',
										width :      this.$scope.outerWidth(),
										height :     1,
										background : 'rgba(0, 255, 255, 0.3)',
										top :        _top + currentItemIcon.outerHeight(),
										left :       0
									} );

						this.$guidelines.append( $guideline );
						this.$guidelines.append( $guideline2 );
					}
				}
			}
		} );
	}
}

const IconGuidelinesControllerInstance = new IconGuidelinesController();

export default IconGuidelinesControllerInstance;



