const STATES = {
				open :       Symbol( 'open' ),
				closed :     Symbol( 'closed' ),

				enabled :    Symbol( 'enabled' ),
				disabled :   Symbol( 'disabled' ),

				fixed :      Symbol( 'fixed' ),
				released :   Symbol( 'released' ),
				
				scrollable : Symbol( 'scrollable' ),
				animating :  Symbol( 'animating' ),

				visible :    Symbol( 'visible' ),
				hidden :     Symbol( 'hidden' ),

				loading :    Symbol( 'laoding' ),
				loaded :     Symbol( 'loaded' ),

				handshaked : Symbol( 'handshaked' )
			},

			StatesContrary = {
				open :    'closed',
				enabled : 'disabled',
				fixed :   'released',
				visible : 'hidden',
				loading : 'loaded'
			},
			
			StatesAll = [];

for ( const key of Object.keys( STATES ) ) {
	StatesAll.push( STATES[ key ] );
}

export default STATES;
export { StatesAll, StatesContrary };



