import AbstractController from './../../../base/controller/Abstract.controller';
import Prism from './../libs/prism';

/**
 * Enables code highlighting in example code blocks
 * @extends { AbstractController }
 */
class SyntaxHightlighterController extends AbstractController {

	constructor () {
		super();
	}

	/**
	 * Initialises the controller singleton.
	 * Has to be called after ready event.
	 *
	 * @public
	 * @returns {void}
	 */
	init ( ) {

		Prism.plugins.NormalizeWhitespace.setDefaults(
			{
				'remove-trailing' : true,
				'remove-indent' :   true,
				'left-trim' :       true,
				'right-trim' :      true
			}
		);
	}
}

/**
 * @private
 */
const instance = new SyntaxHightlighterController();

export default instance;



