import STATES from './../base/constants/States.const';

const StatesAlias = {};

for ( const key of Object.keys( STATES ) ) {
	const uckey = key.charAt( 0 ).toUpperCase() + key.slice( 1 ),
				keyOpen = key.startsWith( 'can' ) ? key : 'is' + uckey,
				openFncs = {};

	openFncs[ keyOpen ] = {
		enumerable : true,
		get :        function () { // eslint-disable-line func-names
			return this.hasState( STATES[ key ] );
		},
		set :        function ( is ) { // eslint-disable-line func-names
			return is === true ? this.setState( STATES[ key ] ) : this.removeState( STATES[ key ] );
		}
	};
	Object.defineProperties( StatesAlias, openFncs );

	StatesAlias[ 'set' + uckey ] = function () { // eslint-disable-line func-names
		this.setState( STATES[ key ] );
	};
	StatesAlias[ 'remove' + uckey ] = function () { // eslint-disable-line func-names
		this.removeState( STATES[ key ] );
	};
}

export default StatesAlias;



