import LOGGER from 'frontend-logger';

function getDebugLoggerEmpty () {
	return {
		warn :  () => {},
		error : () => {},
		info :  () => {},
		log :   () => {}
	};
}

function getDebugLogger ( pref = '★', style = '', detailed = false ) {
	const details = !detailed ? [] : [
					':',
					this.constructor.name,
					'.',
					Object._uid( this )
				],
				args = [
					LOGGER,
					`%c ★ %c %c${pref}%c`,
					style,
					'',
					'font-weight: bold',
					'',
					...details
				];

	return {
		warn :  LOGGER.warn.bind( ...args ),
		error : LOGGER.error.bind( ...args ),
		info :  LOGGER.info.bind( ...args ),
		log :   LOGGER.log.bind( ...args )
	};
}

/**
 * @interface for controller subclasses
 */
export default class DebugController {

	constructor () {
		this.debugStyle = 'color: #f59b00; background: #3c3732';
		this.debug = false;
	}

	debugLocal ( enable, pref = '★', style = '' ) {
		return enable === true ? getDebugLogger.call( this, pref, style || this.debugStyle || '' ) : getDebugLoggerEmpty(); // eslint-disable-line no-constant-condition
	}

	set debug ( is ) {
		if ( is === true || typeof is === 'string' ) {
			if ( is === true ) {
				is = this.constructor.name; // eslint-disable-line no-param-reassign
			}
			this.logger = getDebugLogger.call( this, is, this.debugStyle || '' );
			this._debug = true;
		} else {
			this.logger = getDebugLoggerEmpty( is );
			this._debug = false;
		}
	}

	get debug () {
		return this._debug;
	}

}



