import DebugController from './Debug.controller';

import STATES, { StatesAll, StatesContrary } from './../constants/States.const';

import StatesAlias from './../../helper/StatesAlias.helper';

import Signal from 'signal';

import { invert as _invert, values as _values } from 'lodash';

const allowed = new WeakMap(),
			current = new WeakMap(),
			_onStateChange = new WeakMap();

function triggerStateChange ( which, state ) {
	this.logger.log( 'Trigger state change', which, state );
	_onStateChange.get( this ).dispatch( state, which, current.get( this ) );
}

class StateController extends DebugController {

	constructor ( ...allowedStates ) {
		super();

		this.debugStyle = 'background: #3FFFF7; color: black';
		this.debug = false;

		allowed.set( this, [] );
		current.set( this, [] );

		_onStateChange.set( this, new Signal() );

		Object.getOwnPropertyNames( StatesAlias ).forEach( ( prop ) => {
				Object.defineProperty( this, prop, Object.getOwnPropertyDescriptor( StatesAlias, prop ) );
		} );

		if ( !Array.isArray( allowedStates ) || allowedStates.length <= 0 ) {
			allowedStates = _values( STATES ); // eslint-disable-line no-param-reassign
		}

		this.addAllowedMultiple( ...allowedStates );

		// this.state.setOpen();
	}

// generic functions

	onStateChange ( fn, state = '_' ) {
		if ( typeof fn !== 'function' ) {
			throw new Error( 'Need to pass a function as callback on state change!' );
		}
		this.logger.log( 'Add callback for state change' );
		_onStateChange.get( this ).add( fn, state );
	}

	addAllowed ( state ) {
		this.logger.log( 'Add allowed state', state );
		if ( StatesAll.includes( state ) && !allowed.get( this ).includes( state ) ) {
			allowed.get( this ).push( state );
		}
	}

	addAllowedMultiple ( ...states ) {
		this.logger.log( 'Add multiple allowed states' );
		states.forEach( this.addAllowed.bind( this ) );
	}

	removeAllowed ( state ) {
		this.logger.log( 'Remove allowed state', state );
		if ( StatesAll.includes( state ) && allowed.get( this ).includes( state ) ) {
			allowed.get( this ).splice( allowed.get( this ).indexOf( state ), 1 );
		}
	}

	setState ( state ) {
		this.logger.log( 'Set a current state', state );
		this.logger.log( "\t", 'Performing checks:', StatesAll.includes( state ), allowed.get( this ).includes( state ), !current.get( this ).includes( state ) );
		this.logger.log( "\t", 'Current contents:', "\n", current.get( this ) );
		if ( StatesAll.includes( state ) && allowed.get( this ).includes( state ) && !current.get( this ).includes( state ) ) {
			this.logger.log( "\t", 'Can be added', state );
			const stateContrary = Object.keys( STATES ).reduce( ( obj, key ) => {
				if ( STATES[ key ] === state ) {
					obj = key;
				}
				return obj;
			}, false );

			if ( Object.keys( StatesContrary ).includes( stateContrary ) ) {
				this.removeState( STATES[ StatesContrary[ stateContrary ] ] );
			} else if ( _values( StatesContrary ).includes( stateContrary ) ) {
				this.removeState( STATES[ _invert( StatesContrary )[ stateContrary ] ] );
			}
			current.get( this ).push( state );
			triggerStateChange.call( this, state, 'add' );
			this.logger.log( "\t", 'Current contents after adding:', "\n", current.get( this ) );
			this.logger.log( "\t", 'Added and triggered state change', state );
		}
	}

	setStates ( ...states ) {
		this.logger.log( 'Set current states' );
		states.forEach( this.setState.bind( this ) );
	}

	removeState ( state ) {
		this.logger.log( 'Remove a state', state );
		if ( StatesAll.includes( state ) && current.get( this ).includes( state ) ) {
			current.get( this ).splice( current.get( this ).indexOf( state ), 1 );
			triggerStateChange.call( this, state, 'remove' );
		}
	}

	hasState ( state ) {
		const has = StatesAll.includes( state ) && current.get( this ).includes( state );

		this.logger.log( 'Has a state', state, has );
		return has;
	}

}

export default StateController;



