import DebugController from './Debug.controller';

import StateController from './State.controller';

/**
 * @interface for controller subclasses
 */
export default class AbstractController extends DebugController {

	constructor () {
		super();
		
		this.state = new StateController();
		this.cache = {};
	}

	/**
	 * @abstract
	 * @param {jQuery} $root - root node to attach the controller to
	 * @returns {AbstractController} - instance
	 */
	init ( $root ) {}

}



